<template>
  <div class="text-center w-100 d-flex justify-content-center">
    <input
      class="form-control col-6 mt-5 w-50"
      type="text"
      name=""
      id=""
      v-model="search"
      @keyup.enter="callinfo"
    />
  </div>
</template>

<script>
export default {
  name: "Search",
  data() {
    return {
      search: "",
    };
  },
  methods: {
    callinfo() {
      this.$emit("callinfo", this.search);
    },
  },
};
</script>

