<template>
  <div
    class="
      col-12
      m-auto
      container-fluid
      d-flex
      flex-column
      .align-items-center
      p-3
      home
      holder
    "
    :class="condition"
    v-wow="{ 'animation-name': 'fadeIn', 'animation-duration': '1s' }"
  >
    <Search
      @callinfo="callinfo"
      class="holder"
      v-wow="{ 'animation-name': 'fadeIn', 'animation-duration': '1s' }"
    ></Search>

    <div
      class="d-flex justify-content-center my-5 holder"
      v-wow="{ 'animation-name': 'flash', 'animation-duration': '1s' }"
    >
      <ul class="nav fw-bolder">
        <li class="nav-item">
          <a
            class="nav-link active"
            aria-current="page"
            @click="[allfalse(), (showsearch = true)]"
            >SearchArea</a
          >
        </li>
        <li class="nav-item">
          <a
            class="nav-link"
            href="#"
            @click="[allfalse(), (showrecord = true)]"
            >Record</a
          >
        </li>
        <li class="nav-item">
          <a class="nav-link" href="#" @click="[allfalse(), (showmost = true)]"
            >Most</a
          >
        </li>
      </ul>
    </div>

    <div>
      <div class="most" v-if="showmost">
        <result
          class="holder"
          v-for="(info, key) in mostinfo"
          :key="key"
          :info="info"
          v-wow="{ 'animation-name': 'fadeInLeft', 'animation-duration': '1s' }"
        ></result>
      </div>
      <div
        class="record holder"
        v-if="showrecord"
        v-wow="{ 'animation-name': 'fadeInLeft', 'animation-duration': '1s' }"
      >
        <result v-for="(info, key) in record" :info="info" :key="key"></result>
      </div>
      <div>
        <result
          v-if="info != null && showsearch"
          :info="info"
          class="holder"
          v-wow="{ 'animation-name': 'fadeInLeft', 'animation-duration': '1s' }"
        ></result>
      </div>
    </div>
  </div>
</template>

<script>
import Result from "./components/Result.vue";
import Search from "./components/Search.vue";

export default {
  name: "App",
  components: { Search, Result },
  data() {
    return {
      showsearch: true,
      showrecord: false,
      showmost: true,
      info: null,
      key: "17bb84c84e959f0e7c3bc510cf403d8a",
      api: "http://api.openweathermap.org/data/2.5/",
      showdata: null,
      allcondition: {},
      condition: "rainy",
      icon: null,
      most: ["Yangon", "Mandalay", "London"],
      mostinfo: [],
      record: [],
      recordname: [],
      // currentimg: `//openweathermap.org/img/wn/${this.icon}@2x.png`,
    };
  },
  methods: {
    callinfo(x) {
      this.info = null;
      this.axios
        .get(
          `https://api.openweathermap.org/data/2.5/weather?q=${x}&appid=17bb84c84e959f0e7c3bc510cf403d8a&units=metric`
        )
        .then((response) => {
          this.info = response.data;

          if (!this.recordname.includes(this.info.name)) {
            this.recordname.push(this.info.name);
            this.record.push(response.data);
          }
          if (this.info.main.temp > 25) {
            this.condition = "hot";
          } else {
            this.condition = "cold";
          }
        });
      this.allfalse();
      this.showsearch = true;
    },
    togetmostinfo(x) {
      this.axios
        .get(
          `https://api.openweathermap.org/data/2.5/weather?q=${x}&appid=17bb84c84e959f0e7c3bc510cf403d8a&units=metric`
        )
        .then((response) => {
          this.mostinfo.push(response.data);
        });
    },
    allfalse() {
      this.showsearch = false;
      this.showrecord = false;
      this.showmost = false;
    },
  },
  mounted() {
    this.most.map((el) => {
      this.togetmostinfo(el);
    });
  },
};
</script>

